				neirong1Item:[
					{id:'1',img:'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-cbce6465-a8e8-4255-846c-46383aa22aa0/df91a561-edf2-498f-ad33-eebb0dab5b04.jpg',title:'“治未病中医”与治息息相关，的“未病”是怎么个法',text1:'□',text2:'数码党',text3:'1.2万人浏览',datail:'《黄帝内经》提到：“圣人不治已病治未病，不治已乱治未乱。夫病已成而后药之，乱已成而后治之，譬犹渴而穿井，斗而铸锥，不亦晚乎？”“治未病”的理论记载及临床运用已有2000 多年历史，是古代医家集体智慧的结晶，在炎黄子孙繁衍生息、防病保健中功不可没。<br> 此后，“治未病”思想领域在历代医家精研医理与辨证施治结合的基础上，不断总结，日臻完善，不仅指防病于先，还包含已病早治，瘥后防复，其内容也逐步涉及到饮食起居、情志调节、针灸方药、导引按跷等不同层次。<br> “治未病”与日常生活息息相关。<br>  “治未病”大致相当于我们平常所说的“亚健康”状态，但二者并非完全等同，古医籍虽然以记述医理居多，但中医源于古人生活实践，讲求天人合一，因此“治未病”应该融入我们的日常生活。<br> 比如古人生活上讲“食饮有节，起居有常，不妄作劳”，情志上说“恬淡虚无，真气从之，精神内守”，也就是告诉我们，要按时起居、凡事有度、淡泊名利，这不仅是对行为修养的解读，更是防治“未病”以养“浩然之气”的诠释。所以，对于当代人来说，“治未病”可以理解为外避邪气、内养正气的一种文化内涵。但是，这并不意味着“治未病”可随意用于保健、治疗。<br> 中医“治未病”涉及诸多方面。以灸为例，当前以艾灸养生‘治未病’应用普遍，保健机构应运而生，但灸性温热，所以虚寒性病证、风湿痹证等更为适合，艾灸为中医外治法的组成，同样讲求整体观念、辨证论治，而非部分媒体所宣传的百病皆治。如把握不当，一概混施，就可能有火热伤阴、灼烫皮肤之弊。从长远来看，还降低了民众对其信任度，对中医发展也产生了不利影响。” <br>“治未病”，对症选方妙法多。 <br> '},
					{id:'2',img:'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-cbce6465-a8e8-4255-846c-46383aa22aa0/f3a6d19d-6e3c-43d1-a360-ea1cbd737ed5.jpg',title:'气虚体质如何调理',text1:'□',text2:'数码党',text3:'1.2万人浏览',datail:'气虚体质的人容易发生反复感冒、慢性疲劳综合征、肥胖、消瘦、腹泻、习惯性便秘、内脏下垂、黄褐斑、过敏性鼻炎、小儿呼吸道感染、慢性阻塞性肺病、高血压、冠心病等病症。<br>气虚体质怎么辨。<br>气是维持人体生命活动的基本能量，如同日行千里的蒸汽机需要蒸汽的推动一样。像李阿姨这样气虚体质的人，由于气的推动力不够，体内的能量不足了，就会出现长期疲劳、气短、自汗、水肿、肥胖等表现。<br> 气虚体质特征<br>头身<br>气虚体质的人面色萎黄、无光泽、精力不够、肌肉松软不实、气短懒言，形体或胖或瘦，水肿。由于长期气虚，体内的能量不足，就会精力不够，容易疲劳；气虚不能上荣，所以面色萎黄，无光泽；肌肉得不到气的充养，就会松软而不结实，甚至形体偏瘦；劳则耗气，因此稍微一活动就觉得气不够用，气喘吁吁，即所谓的虚喘；气虚不能正常的水液代谢，这就是中医所讲的“津液不归正化”，津液就变成水湿、痰湿作为垃圾堆在体内，要么形体发胖，要么水肿。气虚体质的人对外界环境适应能力差，不耐受风、寒、暑、湿邪。这是因为气虚质者气虚不能固护肌表，其抵御外邪能力下降所致。临床上常见的季节性感冒也是因为气虚不能耐受各个季节的时令之邪或季节交换时气候的变化造成的。此外，气虚体质之人尚有既不耐受风、寒，同时又不耐受暑热之邪者。之前认识一个老王，凉受不了，热也受不了，特别不喜欢过夏天。因为夏天室外烈日炎炎，暑热难挡，老王在外边待的时间稍微长一些就很容易中暑。室内又到处是冷空调，因此老王夏天很不愿意出门，但每天需要去菜市场买菜，菜市场冷气开得很足，老王每次买完菜就必须马上出来，多待一会儿全身怕冷就会很不舒服。因此，老王都是早上一大早起来就出去买菜。一是因为早上起来没那么热。二是因为早上菜市场冷气也不会太凉。买完菜回来基本就不出门了。<br>饮食<br> 有一女士平时总觉得乏力，到了饭点也不想吃饭，就是吃也吃不多。气虚质的人一般消化功能也较弱，就会出现食欲不振的表现。<br> 睡眠<br>30多岁的小王正是精力充沛事业发展的最佳时期，可他不爱动弹，总有一种疲劳感，老想躺着，说话都有点有气无力。气虚体质的人体内的能量不足，因此很容易疲劳，总想躺着睡觉，可尽管这样，他还是觉得乏力。《黄帝内经》讲：“久卧伤气”，尽管易疲劳，总想睡觉，还是要选适合自己的运动方式动起来，否则越躺气越虚。<br> 心理<br>我们会发现身边总有一些人性格偏内向，喜安静，不喜冒险。有位女同学就是这样，不爱跟别人说话，不喜欢去热闹的地方，睡觉都要拿棉球把耳朵塞住，干什么事情都不会冲在前面，比较有把握的才去做。气虚质人能量不足，心理活动低下，故性格偏内向，喜安静，而不喜冒险。<br> 排泄<br>张师傅是物业维修工，每次见他一干活就出汗，还有些微喘，进一步了解得知张师傅经常会拉肚子。这是因为气虚不能固摄津液，所以容易出虚汗。气虚质人消化吸收能力低下，就容易引起腹泻。另可因气虚导致肠蠕动缓慢，会引起习惯性便秘。这种气虚便秘的人大便并不干硬，或先干后软，虽有便意，但排便困难，用力努挣则汗出气短，便后乏力。<br> 气虚体质怎么调<br>气虚质者多元气虚弱，调体法则为培补元气，补气健脾。<br> 饮食调养<br> 气虚体质宜选用性平偏温、健脾益气的食物，尽量少吃或不吃空心菜、槟榔、生萝卜等耗气食物。不宜多食生冷苦寒、辛辣燥热的食物。由于气虚者多有脾胃虚弱，因此饮食不宜过于滋腻，不能蛮补，否则易导致脾胃呆滞而出现腹胀、食欲不振等。宜食的动物性食物有牛肉、鸡肉、鸡蛋、鹌鹑（蛋）等，谷类及豆类食物如大米、山药、莲子、白扁豆、黄豆、豆腐等，果蔬类食物如南瓜、大枣、胡萝卜、香菇等。<br> 牛肉 性平，味甘，归脾、胃经。《本草拾遗》言其“消水肿，除湿气，补虚，令人强筋骨、壮健”。《韩氏医通》言：“黄牛肉，补气，与绵黄芪同功。”牛肉有补脾胃、益气血、强筋骨等功效，补气之力尤为显著，故气虚质者宜常食之。<br> 鸡肉 性温，味甘，入脾、胃、肝经。《日华子本草》言：“黄雌鸡：止劳劣，添髓补精，助阳气，暖小肠，止泄精，补水气。黑雌鸡：安心定志，治血邪，破心中宿血及痈疽排脓，补心血，补产后虚羸，益色助气。”其性温，具有助阳气、补精髓等作用，对老年人气虚质较为适宜。也可用于病后虚弱之人等。<br> 山药 性平，味甘，归肺、脾、肾经。山药具有补脾养胃、生津益肺、补肾涩精等功效，以山药为主的药膳适合气虚质或兼阴虚质者食用。<br> 南瓜 性温，味甘，入脾、胃经。南瓜具有补中益气、健脾暖胃等功效，适合气虚质者食用。<br> 粳米 性平，味甘，入肺经。《本草纲目》云，五谷之中“唯此谷得天地中和之气，同造化生育之功，故非他物可比”。粳米和五脏，补中益气、健脾养胃、养阴生津、除烦止渴，适合各种体质，尤其适合气虚质者食用。但需注意粳米非精米，目前大家经常食用的米经过精细加工，表面的粗纤维分子、蛋白质、维生素很多被破坏，所以日常生活中应适当食用未经精细打磨的糙米。<br>'},
					{id:'3',img:'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-cbce6465-a8e8-4255-846c-46383aa22aa0/a47d1aa1-c322-4ab5-9702-9835d211470a.jpg',title:'畏寒怕冷的阳虚体质，该如何调理',text1:'□',text2:'数码党',text3:'1.2万人浏览',datail:'阳虚质之人，不仅经常出现畏寒怕冷、手脚发凉、腹泻、关节疼痛等症，此类人群还容易得感冒、咳喘、便秘、水肿、阳痿、痛经、不孕等病。<br>阳虚质怎么辨 <br>阳虚质人群，由于热量不足，体内的“小太阳”不亮了，主要表现为以下5方面特征。<br>头身 面白无光泽，肌肉松软，畏寒怕冷，以胃脘、背部、腰膝多见，手足发凉，舌淡胖嫩；耐夏不耐冬，易感风、寒、湿邪。<br>饮食 喜热食。<br>睡眠 易困思睡，精神不振。<br>心理 性格内向 多沉静。<br>排泄 大便溏薄，易出冷汗。<br>阳虚质怎么调 <br>阳虚质者多元阳不足，调体法则为补肾温阳，益火之源。由于督脉能总督一身之阳气，为“阳脉之海”。故阳虚质者应注意督脉的温通与调护。<br>饮食调养 <br>阳虚质者宜选用甘温补脾阳、温肾阳为主的食物。少食生冷、苦寒、黏腻食物，如田螺、螃蟹、海带、紫菜、芹菜、苦瓜、冬瓜、西瓜、香蕉、柿子、甘蔗、梨、绿豆、蚕豆、绿茶、冷冻饮料等。即使在盛夏也不要过食寒凉之品。<br>以下介绍几个适合阳虚质者的食材。<br>羊肉 性温，味甘，归脾、肾经。《本草纲目》言羊肉“暖中补虚，补中益气，开胃健力，益肾气”。羊肉具有益气补虚、温中暖下、补肾壮阳的作用，尤其适合阳虚质，特别是老年体衰者冬令进补食用。<br>凡有口舌生疮、牙齿肿痛、咳吐黄痰、牙痛等症状者不宜食用；患肝病、高血压、急性肠炎或其他感染性疾病及发热期间不宜食用；外感病邪，素体有热者慎用。暑热天慎食之；水肿、骨蒸、疟疾、外感及一切热性病症者禁食。<br>胡桃仁 味甘，性温，入肺、肝、肾经，能补肾助阳，温肺定喘，润肠通便。适合阳虚质者食用。胡桃仁适合生吃、水煮、糖蘸、烧菜等多种食法。<br>胡桃仁的重量约20～30克，每天吃5～6个为宜。如吃得过多，会生痰、恶心，严重者会有严重的腹泻，甚至水样大便。胡桃仁不能与野鸡肉一起食用，不可与酒同食。凡阴虚火旺、痰热咳嗽、便溏腹泻、素有内热盛及痰湿重者均不宜食用。<br>韭菜 韭菜又称“起阳草”，味甘、辛，性温，无毒，入肝、胃、肾经。韭菜具有补肾壮阳、温中行气、健脾开胃等功效，适合老年人阳虚质有腰膝冷痛、食欲不佳者常食。韭菜还有行气活血散瘀作用。<br>生姜 性温，味辛，入肺、胃、脾经，具有内能温胃止呕、温肺祛痰，外可发散表寒等功效。适合阳虚质易患呕吐、咳喘、风寒感冒者食用。亦可作为健脾开胃的辅助调养。阳虚质者可在做菜的时候放姜，还可口嚼生姜，甚至还可把生姜切片以后放在脐上，也能生姜煮水泡脚。<br>下面介绍几款阳虚质养生药膳。 <br>当归生姜羊肉汤<br>【原料】当归20克，生姜30克，羊肉500克。<br>【制作】当归、生姜冲洗干净，用清水浸软，切片备用。羊肉剔去筋膜，放入开水锅中略烫，除去血水后捞出，切片备用。将当归、生姜、羊肉放入砂锅中，加清水、料酒、食盐，旺火烧沸后撇去浮沫，再改用小火炖至羊肉熟烂即成。<br>【效用】温中补血，祛寒止痛。适合阳虚质容易出现怕冷、腰膝酸软、痛经、月经量少者食用。<br>韭菜炒胡桃仁<br>【原料】胡桃仁50克，韭菜200克。<br>【制作】胡桃仁开水浸泡去皮，沥干备用。韭菜择洗干净，切成寸段备用。麻油倒入炒锅，烧至七成热时加入胡桃仁，炸至焦黄，再加入韭菜、食盐，翻炒至熟。<br>【效用】温肾助阳。适合阳虚质容易出现腰膝冷痛、阳痿者食用。<br>羊肉羹<br>【原料】羊肉250克，萝卜1个，草果3克，陈皮3克，高良姜3克，胡椒3克，荜茇3克，葱白3克，生姜少许。<br>【制作】羊肉剔去筋膜，洗净后入沸水锅内汆去血水，捞出后再用凉水漂洗干净，切成约1厘米见方的肉丁；萝卜洗净泥土，切成厚0.3厘米的片；将草果、陈皮、高良姜、荜茇用洁净的纱布袋装好并扎口；胡椒拍破，葱白切成节，生姜洗净拍破。羊肉丁和以上药物同置炒锅中，加入清水适量，并加葱和生姜，旺火烧沸，打去浮沫，再用文火煨2～3小时，至肉酥烂即可。捞出药包，除去葱和生姜，略调味即成。<br>【效用】温肾助阳，散寒止痛。适合阳虚质易发胃脘冷痛、大便溏泻者食用。<br>肉苁蓉粥<br>【原料】肉苁蓉60克（酒浸一宿，刮去皱皮，细切），粳米100克，鹿角胶15克（切碎，炒令黄燥，为末），羊肉120克（细切），葱、姜、盐各少许。<br>【制作】煮羊肉、肉苁蓉、粳米作粥，临熟，下鹿角胶末，用盐、酱、味精调和食之。<br>【效用】补肾温阳，润肠通便。阳虚质容易出现便秘、小便清长、夜尿频多者适宜食用。中老年人阳虚质者尤为适宜。<br>肉桂鸡肝<br>【原料】肉桂5克，鸡肝，生姜、葱、料酒、味精各适量。<br>【制作】肉桂洗净，切成长2厘米、宽1厘米的块；鸡肝洗净，一破4片，放入瓦锅内，加入葱、生姜、食盐、料酒、清水各适量。将瓦锅置入盛有水的锅中，隔水炖至鸡肝熟即成。食用时加味精少许。<br>【效用】温补肾阳。适合阳虚质易发手足冰冷、脘腹冷痛、夜尿频多者食用。<br>形体锻炼 <br>运动避风寒。宜在阳光充足的环境下适当进行舒缓柔和的户外活动，尽量避免在大风、大寒、大雪的环境中锻炼。日光浴、空气浴是较好的强身壮阳之法。也可选择八段锦，在完成整套动作后将“五劳七伤往后瞧”和“两手攀足固肾腰”加做1～3遍。<br>阳虚质的人怕冷，比较容易受风和寒的侵袭，锻炼时应注意保暖避寒。要选择暖和的天气，进行户外运动锻炼，不宜在阴冷天气或潮湿之处锻炼身体，如在水中游泳易受寒气和湿气，一般不适合阳虚质的人。<br>日光浴、空气浴是较好的强身壮阳之法。根据中医“春夏养阳，秋冬养阴”的观点，阳虚质的人锻炼时间最好选择春夏，一天中又以阳光充足的上午为最好时机，其他时间锻炼则应当在室内进行。冬季要避寒就温，春夏要注意培补阳气，做到“无厌于日”，即在春夏季多晒太阳，每次不得少于30分钟。<br>阳虚质的人也可经常按摩督脉上的穴位，如长强、腰俞、命门等，可以起到疏通阳气、强身健体的作用。<br>精神调摄 <br>心态要阳光，音乐宜激昂。阳虚质的人，性格沉静、内向。因此，宜保持积极向上的心态，正确对待生活中的不利事件，及时调节自己的消极情绪，尽量避免和减少悲伤，还要防止惊恐、大喜大悲等不良情绪的影响。在日常生活中，可多听诸如《黄河大合唱》等激昂、高亢、豪迈的音乐，还可以选择一些优美、畅快的旋律、轻音乐。<br>起居调护 <br>起居要保暖。居住环境以温和的暖色调为宜，不宜在阴暗、潮湿、寒冷的环境下长期工作和生活。阳虚质的人，由于机体阳气不足，失于温煦，故在日常起居中要注意避寒取暖，养护阳气。特别是冬季要适当多穿衣服，尽量吃温热的食物，尤其要注意背部、腰部和下肢的保暖。<br>白天阳气充足，故阳虚质者在白天应保持一定的活动量，激发体内阳气。阳虚无力气化蒸腾水液，故睡前尽量不要饮水，且要排空小便，以免引起浮肿或频繁起夜影响睡眠。<br>坚持睡前用热水泡脚，或刺激足部穴位促进气血运行，就可驱除寒气，舒通全身经络，增强人体免疫力和抵抗力，具有呵护阳气、强身健体的功效。泡脚时，用40～50℃的水，水量以淹没踝部为好，双脚浸泡15分钟。同时，用手缓慢、连贯地按摩双脚，直至自己感觉双脚微微有发热感为止。<br>针灸推拿 <br>【选穴】百会、肾俞、气海、关元、足三里。<br>【简便取穴】百会：两侧耳尖连线之中点取之。肾俞：背部，第2腰椎棘突下，旁开1.5寸。气海：前正中线，脐下1.5寸。关元：气海下1.5寸，即脐下3寸。足三里：胫骨外侧，外膝眼下3寸。<br>【功效】百会具有益气升阳之效，关元、气海具有培元固本、补益下焦之功。三穴合用既可交会任督二脉，又可益气培元、升举阳气。肾为先天之本，取肾俞可补益肾气；脾胃为后天之本，取足三里可调理脾胃，补益气血，使后天得以充养先天，故诸穴合用可使气血渐旺，阳气渐充。<br>【操作】百会用平刺法，留针30分钟，不行针。其余穴位可行针刺补法，或正面、背面交替使用温针灸。居家保健可用温和灸方法，点燃艾条或借助温灸盒，对穴位进行温灸，每次10～15分钟，以皮肤微微潮红为度。每周进行1～2次。关元还可采用掌根揉法，按揉每穴2～3分钟，每天1～2次。也可配合摩擦腰肾法温肾助阳，以手掌鱼际、掌根或拳背摩擦两侧腰骶部，每次操作约10分钟，以摩至皮肤温热为度，每天1次。'},
					{id:'4',img:'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-cbce6465-a8e8-4255-846c-46383aa22aa0/0b0c58c6-ed19-40a7-bd8e-af7216ad64a9.jpg',title:'中医提升免疫疗法多，日常生活重养生!!!',text1:'□',text2:'数码党',text3:'1.2万人浏览',datail:'新冠肺炎的来袭，再一次让人们关注如何维护好自身免疫力、提高防病抗病能力的问题。医学上所说的免疫力，是指人体免疫系统识别和清除外源性异物、进行自我保护的一种能力。对于普通人而言，免疫力低下一般都只会在一定的条件下、在一定的时期内发生。如劳累、睡眠少、嗜烟酗酒、久坐少动、情绪低落、焦虑、紧张、饮水少、饮食不规律、维生素缺乏等。一个人如果经常罹患感冒，且有倦乏、消化功能紊乱、唇周疱疹（俗称“上火”、嘴角起泡）等情况，就说明免疫力出现了“滑坡”，必须引起高度警觉和重视。中医增强免疫力低下的总体思路是“三通”：即通过补益气血、养正固表来增大抗病能力；通过解毒祛浊、开窍畅中来协调免疫效果；通过益肾养阴、潜镇安神来避免过度亢奋所造成的自身损伤。<br>中医疗法益处多 <br>中医认为，提升免疫力是一个系统工程，不是一方一法就可以解决的，要采取因人、因地、因时的辨证策略。治则治法有很多种，每一种治法都有各自的适应症，针对的都是不同的体质状态，很多时候需要采用多种治则、治法和方式来施行。在此，贾主任详细地介绍了如下十余种中医疗法。<br>补气法：中气不足、气虚虚弱者，表现为气短声低、眩晕乏力、面黄少华、唇淡或黯、食欲不振、食后腹胀、时有便溏，舌嫩苔厚，脉虚等。常用的方剂有四君子汤、补中益气丸、参苓白术散、生脉口服液、升陷汤；可使用的中草药有人参、西洋参、党参、太子参、黄芪、大枣、刺五加、沙棘等。<br>补血法：营血亏虚者，表现为头晕眼花、心悸失眠、面白唇淡、爪甲枯瘪、大便干燥，舌质淡红、苔滑少津、脉细涩或数等。常用的方剂有四物汤、归脾丸、十全大补丸。可使用的中药有当归、熟地、阿胶、龙眼肉等。<br>固表法：针对肌表疏松、卫外不固者，表现为易患感冒、体弱怕风、自汗盗汗、面色?白、心悸短气，舌淡、脉虚细弱等。常用的方剂有玉屏风散、芪风固表颗粒、举元煎等。可使用的中草药有黄芪、白术、刺五加等。<br>健脾法：脾胃虚弱、运化无权者，表现为虚乏倦怠、头身困重、食欲不振、消化不良、胸腹痞胀、便秘或溏薄、夜寐多梦，舌淡、苔腻、脉虚弱等。常用的方剂有保元汤、健脾丸、人参养荣丸、黄芪建中汤。可使用的中草药有党参、茯苓、白术、甘草、芡实、山药、陈皮、山楂等。<br>醒脾法：针对脾运失权、精微失布者，常见人群有肥胖、高脂血症、高尿酸血症等。表现为头晕健忘、昏沉倦懒、消化不良、时有粘汗、食后困睡、体力耐力下降、舌淡胖、齿痕重、苔腻、脉虚或濡或滑等。常用的方剂有实脾散、香砂枳术丸、资生丸、四磨汤口服液。可使用的中草药有石菖蒲、佛手、苏合香、香附、厚朴、藿香、陈皮、砂仁、枳壳、冰片等。<br>除湿法：针对湿盛浸淫、阻碍气机者。表现为头重身痛、关节酸楚、面浮肢肿、脘腹痞闷、时时泛恶、舌苔白腻、脉紧或缓等。常用的方剂有参苏饮、葱豉汤、九味羌活汤。可使用的中草药有羌活、独活、杜仲、牛膝、威灵仙、木瓜、桑寄生、草果、薏苡仁、荠菜等。<br>降浊法：针对浊毒内蕴、阻塞孔窍者，糖尿病、肥胖、高脂血症、高尿酸血症为多见。表现为头昏倦懒、虚乏汗出、午后周身不适、身热不扬、时有咽肿目赤、食后困睡、便粘尿浊、体力耐力下降，舌紫或暗、苔厚腻、脉滑。常用的方剂有甘露消毒丹、半夏泻心汤、连朴饮。可使用的中草药有石菖蒲、威灵仙、厚朴、槟榔、木香、菊花、佩兰等。<br>活血法：针对瘀血内停、脉络不畅者。表现为头昏耳聋、身痛且有定处、急躁善怒、入暮潮热、夜寐多梦、面青唇紫、时有干呕，舌质暗红、有瘀点或者瘀斑、脉涩或弦紧等。常用的方剂有通窍活血汤、补阳还五汤、血府逐瘀汤。可使用的中草药有当归、丹参、赤芍、川芎、地龙、三七、玫瑰花等。<br>益肺法：针对肺气不足、久咳不愈者。表现为感冒咳喘等病久不愈或反复发作、虚乏易感、胸闷短气、动则喘甚、喘促自汗，脉虚数等。常用的方剂有补肺汤、补中益气丸、九仙散。可使用的中草药有黄芪、防风、泽泻、大枣、白术、升麻、虫草等。<br>宣肺法：针对肺气不宣、久咳不愈者。表现为病势缠绵、干咳少痰、咽干喉紧、胸闷痰粘、怕风畏寒、自汗盗汗、心悸短气、舌淡脉虚等。常用的方剂有清肺汤、桔梗汤、加味甘桔汤。可使用的中草药有桑白皮、桔梗、杏仁、升麻、细辛、麻黄等。<br>疏肝法：针对肝郁脾虚、脏腑不和者。表现为情志不舒、头痛目眩、寒热错杂、口苦呃逆、胃腹痞满、月经不调，舌红、苔黄、脉弦等。常用的方剂有小柴胡汤、逍遥丸、半夏泻心汤。可使用的中草药有柴胡、郁金、合欢皮、川楝子、陈皮、酒白芍、茵陈、栀子等。<br>解毒法：针对热毒壅盛、脏腑郁热者。表现为抑郁烦躁、口干舌燥、倦怠乏力、疮疡肿痛、阴痒潮湿、男子尿浊遗泄、女子带下浊臭，舌红、苔黄、脉数等。常用的方剂有栀子豉汤、清心莲子饮、清暑益气汤。可使用的中草药有黄连、栀子、金银花、菊花、蒲公英、连翘、莲子、鱼腥草、木蝴蝶、绿豆、芦荟、蜂房、大蒜等。<br>固涩法：针对气血、精津滑脱耗散者。表现为体虚羸弱、自汗盗汗、心悸短气、肺虚久咳、久泻失禁、遗精带下，舌淡、脉虚细弱等。常用的方剂有真人养脏汤、牡蛎散、九仙散、生脉饮、六味地黄丸。可使用的中草药有山茱萸、五味子、乌梅、肉豆蔻、莲子、芡实、薏苡仁、益智仁等。<br>安神法：针对五脏功能失调、神志不安者，常见免疫功能紊乱、易患过敏性疾病等。表现为惊恐善怒、心神烦乱、健忘心悸、失眠多梦、耳鸣眼花、口舌生疮，舌红、少苔、脉细数等。常用的方剂有朱砂安神丸、天王补心丹、左慈丸等。可使用的中草药有茯神、龙骨、牡蛎、酸枣仁、龙眼肉、灵芝、远志等。<br>开窍法：针对浊瘀交阻、蒙蔽孔窍者。临床多为糖尿病、心脑血管病、高脂血症、高尿酸血症等。表现为情绪不畅、头晕健忘、昏沉倦乏、肢体困重、入夜尤甚、多梦嘈杂、胸闷心悸、消化不良、舌暗红或紫、苔浊腻、脉弦滑等。常用的方剂有菖蒲郁金汤、不忘散。可使用的中草药有苏合香、安息香、石菖蒲、天麻、僵蚕、雄蚕蛾、火麻仁、郁金、细辛等。<br>日常生活重养生 <br>在详解上述方法的同时，注重四时养生可有效增进免疫力。根据四季变化特点而调整起居劳作方式，做到起居有常、劳逸结合，自然益处多多。贾教授解释，春季晚睡早起，在庭院里散步、舒缓身体，能够使神志随升发之气而舒畅，无形中培育了抗病御邪之力；夏季晚睡早起，可使腠理宣统、阳气疏泄于外，可免受外邪感染；秋季早睡早起、保持意志安定，能使精神内守、不急不躁，从而稳住免疫功能“基本盘”；冬季早睡晚起、避开寒凉、保持温暖，能避免阳气频繁耗伤，并刺激免疫物质的生成。<br>那么，日常生活中避免免疫力降低的办法有哪些呢？一是要合理膳食、均衡饮食、规律进食，保证足够的能量和营养。人体内每天都要产生数以百亿计的免疫细胞，而产生这些细胞所需要的原材料都需要经过人们的饮食中的能量、维生素和矿物质来获得。二是讲究适当和有规律的运动。运动能促进血液循环和免疫细胞运输，每天坚持适度的身体锻炼能减轻感染的概率。三是养成良好的生活习惯，要戒烟限酒，规律作息，心情舒畅，乐观应对困难，不过给自己过多压力。<br>与此同时，专家还向大家推荐了如下增强免疫力的时蔬：<br>五谷有燕麦、大麦、红薯、薏米、麦芽；蔬菜有胡萝卜、卷心菜、西兰花、菠菜、大蒜、蘑菇、香菇；水果有西瓜、猕猴桃、樱桃、木瓜、香蕉、葡萄、雪梨、苹果。'},
					{id:'5',img:'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-cbce6465-a8e8-4255-846c-46383aa22aa0/552b39c8-2db1-49c6-8c97-fc78975a5286.jpg',title:'七情均可致失眠，心病还需心药医',text1:'□',text2:'数码党',text3:'1.2万人浏览',datail:'中医认为，情志活动与脏腑功能互为影响，脏腑气血是情志变化的物质基础，情志活动也是脏腑机能活动的表现形式之一。七情的正常活动对机体生理功能有着重要的调节作用，强烈的情志刺激和长期的负面情绪和心态是导致情志病发生的主要原因。<br>根据中医理论，怒则气上，喜则气缓，悲则气消，恐则气下，惊则气乱，思则气结。几种情志变化反应过于强烈，会使人体气机紊乱，情志失调，均可能导致失眠的发生。一般因情志因素导致的失眠有以下几种类型。<br>烦躁型失眠 <br>成书于东汉时期的中医经典著作《伤寒杂病论》是中国最早的理论联系实际的临床诊疗方书，又分为《伤寒论》与《金匮要略》两部。在《金匮要略·血痹虚劳病脉证并治》中有言，“虚劳虚烦不得眠，酸枣仁汤主之”。由此处可以了解，“虚烦”与失眠有关。此型失眠患者体质易虚，且因琐事所累而易于烦躁，影响睡眠，主要表现为入睡困难、烦躁、心悸、易汗出，是失眠中较为常见的一种类型。<br>抑郁型失眠 <br>此类不寐与西医抑郁症伴失眠相类似，患者性格多偏于文静、细腻、内敛，不喜欢向他人袒露心事，因此很容易闷闷不乐，气郁于胸中而致失眠，主要表现为睡后易醒，醒后不易再次入睡或早醒。<br>思虑型失眠 <br>思虑过度与西医的认知障碍、强迫思维等相类似，因思虑过多、平日思绪频发而影响睡眠者，常表现为整夜不寐，睡眠维持障碍。此型失眠患者往往敏感多思，易于内化心理冲突 , 这是失眠易发的一个重要特征。<br>惊悸型失眠 <br>惊惕也可导致失眠，此型失眠与西医学焦虑症伴失眠类似，主要表现为失眠多梦、易惊醒，醒后冷汗淋漓、心悸等。患者常常柔弱胆怯，遇事容易惊慌或恐惧，这是失眠发生的一个重要因素。<br>俗语云，“心病还需心药医”，对于情志因素导致的失眠，患者还应学会心理调适，积极应对生活中的事件。另外，患者还应注意保持健康的心境，不要因失眠而造成心理负担，害怕失眠，从而加重失眠。应学会放松、接受自己当下现状，不去评判。另外，也可以在日常生活中，通过改变外部环境，比如选择柔软舒适的床铺、柔和暗淡的灯光，睡前避免剧烈运动、大量进食等来改善睡眠品质。同时，睡前 1 小时内不做容易引起兴奋的脑力劳动或观看容易引起兴奋的书籍和影视节目等。'},
					{id:'6',img:'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-cbce6465-a8e8-4255-846c-46383aa22aa0/7d22d4ba-ff5c-4925-adb5-bbd1eb41ac5c.png',title:'冬季究竟怎么养，防寒养阳五锦囊',text1:'□',text2:'数码党',text3:'1.2万人浏览',datail:'冬天悄然来临，随着气温骤然下降，此时也进入敛阳、补阳的好时节，阳气虚弱、气血不足的人更应该顺应自然规律，补肾防寒护阳气。冬季究竟该怎么养？专家给出以下五大养阳锦囊。<br>锦囊一：冬季常晒背<br>人体腹为阴，背为阳，心肺主之，背部的诸多穴位经络，尤其是最重要的督脉（位于后背正中线上），经常晒晒这些穴位能起到强壮心肺、调理脏腑气血的作用。但是晒太阳也有讲究，如果在阴天，日无光明，无光则阳气不足，阳中带阴，则不可以晒，要选日有光之时，一般上午11点到下午1点晒太阳最佳，此时空气较好，使人感到温暖柔和，时间20分钟到30分钟为宜，同时该时间段属午时，心经最旺，午时阳气最亢盛，午时过后则阴气渐盛，在阴阳交换的时候，宜稍作静养，此时保养心经则事半功倍。因此，在午时若能稍睡片刻，不仅对于养心大有好处，也可使下午工作精力充沛。<br>锦囊二：常揉三穴位<br>人体自带“阳气窝”，冬日每天揉一揉以下三个穴位，不仅在疾病治疗方面疗效明显，在补充阳气、强身健体方面也同样发挥着重要的作用。<br>涌泉穴：涌泉穴的位置位于足底部，蜷足时足前部凹陷处，约当足底第2、3跖趾缝纹头端与足跟连线的前1/3与后2/3交点上。每天晚上睡觉前可以放松地坐在床上，然后用手掌来回揉搓脚底，最好能够揉搓到脚底发热，然后再用大拇指的指腹位置点按涌泉穴，等到有酸痛感时可以停止换另一只脚，有助缓解手脚冰凉情况。<br> 关元穴：关元穴位于人体“阴脉之海”的任脉，前正中线上，肚脐直下三寸处。古人描绘关元为男子藏精、女子蓄血之处，可见关元穴的重要性。每天可以关元为圆心，左或右手掌做逆时针及顺时针方向摩动3~5分钟。然后用双手食指指腹重叠在一起，点按穴位20次，以局部有酸胀感为宜，经常按摩能起到补益元气、强身固精、祛寒回阳的效果。<br> 三阴交：此穴为足太阴脾经、足少阴肾经、足厥阴肝经交会之处，位于小腿内侧。经常用指腹按摩三阴交这个穴位，除可健脾益血外，也可调肝补肾，亦有安神之效。<br>锦囊三：少食生冷 <br>《遵生八笺·冬季摄生消息论》中指出，“冬月阳气在内，阴气在外，老人多有上热下冷之患。” 立冬后天气逐渐转寒，寒为阴邪，易伤人体阳气，而阳气根源于肾，故寒邪最易中伤肾阳，所以此时要重视养肾、温肾，宜多食温热补肾的食物，以提高人体御寒能力。<br>肾阴虚者容易出现口干舌燥、小便黄、大便干、精神状态差等症状，需滋阴补肾，可多食枸杞子、银耳等食物；肾阳虚者常常表现为面色苍白、畏寒怕冷、手脚冰凉、精神不振、失眠健忘、腰膝酸软，夜尿频多等，宜补阳益肾，食羊肉、牛肉、桂圆、红枣等滋补品。<br>锦囊四：强身健体 <br>冬季宜养精蓄锐，避免高强度运动，保存阳气，但也不能不动，最佳选择是进行有氧运动。推荐练习八段锦、太极拳等动作柔和的养生功法，有滋阴助阳、培元补气的功效。与此同时，随着温度的降低，身体各器官的功能，包括肌肉、关节，都处在一个代谢较低的水平，肌肉和韧带也都比较僵硬，所以在运动前应该进行一定时间的准备活动。<br>锦囊五：拒绝美丽“冻”人 <br>冬季里，有些爱美人士可能会忽视保暖问题，但颈部、背部、腹部、脚切记要重点保暖。可通过穿高领毛衣、围巾避免头颈部受寒；对于女性，腹部温暖是避免痛经的有效方式之一，借助一些动作按摩护肾，如双手搓腰，特别是脊椎两旁的后腰，可以温煦肾阳、畅达气血；足部防寒首先要避免露脚踝，脚上穴位较多，一旦受寒邪侵袭，会导致身体出现不适，有条件的话可以常做足浴并进行按摩。'},
					{id:'7',img:'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-cbce6465-a8e8-4255-846c-46383aa22aa0/1054aba1-386c-45f9-8265-7de837af813b.jpg',title:'文人墨客笔下生诗歌亦能疗疾病',text1:'□',text2:'数码党',text3:'1.2万人浏览',datail:'提起“诗药”治病，读者诸君定会以为这是令人难以置信的“天方夜谭”，近年来在国外传媒时有传闻，而且言之凿凿，使你不得不信。<br> 一是据英国权威报纸《泰晤士报》披露：1882年英国著名医生西摩·布利曾用《爱的深化》诗集为“诗药”处方，让病人尽情朗诵，结果治愈了多例抑郁症病人。<br> 二是据美国《纽约时报》载：1959年，美国妇女马茜·辛普森思患乳腺癌，手术后，遵照医生开的“诗药”处方，坚持吟诗疗疾，并学会写诗，以此来消除癌魔造成对身心的阴影，一年后，果然得到了康复。<br> 三是据日本《朝日新闻》报道：在拥有5700万人口的意大利，会写诗的竟拥有百余万人，其中绝大多数人，写诗并不是为了向报刊投稿或结集出版。而是以诗言志、言情、助兴、消愁、治病。凡是到过意大利的人，就会发现当你走进书店，就可以买到如药品一样装潢的“诗药盒”，上面有标明经国家医疗卫生部门核准的字号及“主治”、“禁忌”、“日服量”等字样，盒内装的是印刷十分讲究的精美诗篇，其中不少名家作品，有的则是诗药制药厂自制的产品，一个疗程往往要服用一个系列的诗药，其精心设计的配方均出自著名病理学家和文学家之手。这些诗药主治失眠、健忘、神经衰弱、食欲不振、情绪紊乱、精神分裂、抑郁症等病症。服诗药，主要是吟诗，犹如做健身体操，它既要求发音准确，又要求有正确的站立挥臂姿势，反复吟诵多少篇多少遍都有严格要求。<br>其实，“诗药”并非洋人“首创”。对悠悠中华诗国来说，本不是陌生的事。<br> 最早见于西汉淮阴才子、著名辞赋家、官居弘农都尉的枚乘的名赋《七发》。记叙了吴客不是用“药石针刺灸疗”，而是采用“要言妙道”，即精深的言辞、美妙的哲理诗赋来治病，终于使“楚太子据几而起，海然汗出，霍然病愈”。<br> 另见《全唐诗》载：东川才子、新乡县尉李顽的《圣善阁送斐迪入京》里有“清吟可愈疾，携手暂同饮”之说。<br> 三见于《陆放翁全集》载：南宋爱国诗人陆游，晚年在越州山阴(今浙江绍兴)家乡隐居，“身杂老农间”，十分关心百姓疾苦，一边在村头树下与乡民饮酒吟诗，一边为老农看病施药，在他的《山坟经行因施药》诗中就留下“儿扶一老候溪边，来告头风久未痊。不用更求芎芷药，吾诗读罢自醒然。”的名句。身为诗人兼名中医的陆游，他鼓吹以诗药来治疗头风痛，显然是有一定根据的。在当代文坛上也有以诗药疗疾的趣闻。那是著名诗人贺敬之，1992年5月于杭州作《富春江散歌》，其小序云：“我于去年体检发现重疾入医院治疗，今春出院赴杭州疗养。行笔仍如以往不拘旧律，因此以‘散歌’名之。待向方家求教前，姑自书、自诵之，抑疗病之一法耶。”诗中有两句“长啸畅笑消病颜，云月八千有此缘”，真可谓深得其中三昧。<br>为什么“诗药”能疗疾？这是由于诗的本身具有特殊内涵决定的。由于语言是一种特殊符号信息，而诗赋又是语言特殊符号信息中经过诗人千锤百炼的结晶与高度浓缩，它具有强烈的艺术感染力，其对人的大脑“条件反射”与“心灵感应”远非一般语言符号信息所能匹敌的。所以随着人在反复吟咏诗赋，在大脑皮层必然会激起阵阵感情漪澜，在心理与生理机制上引起特殊效应——共鸣。使心境与美妙宏阔、清新、幽深的诗境融为一体，陶冶了心灵与情操。正如著名美学家朱光潜所指出的：“诗的境界在刹那间见终古，在微光中显大千，在有限中寓无限。”这时，人的心情完全沉醉于诗境馈赠的健康情绪之中，自然而然地把一些心理疾病隐患冲洗掉。从当代医学角度上来看，科学家、病理学家们在从事诗药研究后得出的结论是：朗诵诗不仅能增加肺活量，而且能被其艺术魅力所感染，令人“忘我地”进入角色，使大脑与相关的神经敏感度大大提高，血液循环加快，酶与乙酞胆碱等健康激素和其它生物活性物质分泌增加，血流量与大脑神经细胞调节到最佳状态，促使体内新陈代谢更加旺盛。<br> 所以说诗药，犹如欣赏美画、欣赏动听的音乐，在欣赏诗的艺术美中，使人的脑细胞得到激发，促进其分泌对人体有利的激素，增强“御敌”能力，从而达到祛病健身的目的。这就是集艺术与医学之道于一体的“诗药”的魅力所在。'},
					{id:'8',img:'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-cbce6465-a8e8-4255-846c-46383aa22aa0/677ee7da-f3af-456d-88c0-aa59ae59ca53.jpg',title:'温泉泡脚虽好但“集体泡脚”须慎重',text1:'□',text2:'数码党',text3:'1.2万人浏览',datail:'近日，在某市某公园新设立的一个温泉体验区中，许多人挽起裤脚在池子里泡脚，免费体验“温泉文化”。每天温泉池中的水会定时更换，附近老人抱着养生的心态经常过来泡泡脚，希望能促进血液循环。随着体验人数的增加，温泉池中的水有些浑浊，也有人对水质情况表示担忧。这样泡脚是否会传染病菌呢？<br> “泡温泉的水如果是活水，且温度达到60℃以上，那么传染疾病的风险是比较低的。”专家表示，巴氏消毒温度在64℃，大部分病菌可以被灭活。不过露天温泉池的温度通常偏低，如果这么多人集中泡脚，传染脚上病菌的可能性也会增加。而且泡脚时一般都光着脚踩在地面上，也有可能交叉传染病菌。<br> 泡脚虽好，但“集体泡脚”须慎重。专家提醒说，尤其是脚上有伤口、皮肤炎、皮肤溃疡或严重感染者，不建议尝试这样的体验。另外，在专业浴所“汤屋”泡温泉、汗蒸、盐浴，对皮肤有一定的好处，尤其是含有硫磺成分的温泉。但对于皮炎、湿疹患者来说，长时间处于高温的环境容易增加皮肤干燥程度，同时如果温泉场所消毒不干净，很可能染上病菌，加重皮炎、湿疹的症状。<br> 还需特别提醒的是，民众在进行休闲新时尚的“鱼疗”享受时，要小心鱼传病。进行“鱼疗”时人脚一入池，小鱼儿就会立即围上来“吻”个不停，这些鱼以死皮细胞为食。不少商家都宣称，“鱼疗”可有效去除人体新陈代谢产生的皮肤角质、细菌。事实上，鱼的体内可能会携带一些病菌和寄生虫，例如海鱼分支杆菌，这种菌在海鱼、淡水鱼中都存在。如果被携带这种病菌的鱼咬，病菌就会随着伤口进入皮肤，皮肤会出现结节、破溃甚至化脓的情况。'},
